/* Слайдер */
/* Слайдер запущен */
/* Слайдер с точками */
/* Ограничивающая оболочка */
.slick-list {
  overflow: hidden;
}

/* Лента слайдов */
.slick-track {
  display: flex;
}

/* Слайд */
/* Слайд активный (показывается) */
/* Слайд основной */
/* Слайд по центру */
/* Клонированный слайд */
/* Стрелка */
.slick-arrow {
  height: 75px;
  width: 75px;
  border-radius: 50%;
  position: absolute;
  font-size: 0;
  background-color: #1f4590;
}

/* Стрелка влево */
.slick-arrow.slick-prev {
  right: 150px;
  z-index: 500;
}
.slick-arrow.slick-prev::before {
  content: url(/app/img/arrow_back_ios_24px.svg);
}

/* Стрелка вправо */
.slick-arrow.slick-next {
  right: 50px;
}
.slick-arrow.slick-next::before {
  content: url(/app/img/arrow_forward_ios_24px.svg);
}

